import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import red from "@material-ui/core/colors/red";

import MoreVertIcon from "@material-ui/icons/MoreVert";
import SkipPreviousIcon from "@material-ui/icons/SkipPrevious";
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import SkipNextIcon from "@material-ui/icons/SkipNext";
import PauseIcon from "@material-ui/icons/Pause";
import ReplayIcon from "@material-ui/icons/Replay";
import SurahLists from "./surahlist";
import Slider, {
  defaultValueReducer
} from "@material-ui/lab/Slider";

function valueReducer(rawValue, props, event) {
  const { disabled, max, min, step } = props;

  function roundToStep(number) {
    return Math.round(number / step) * step;
  }

  if (!disabled && step) {
    if (rawValue > min && rawValue < max) {
      if (rawValue === max - step) {
        // If moving the Slider using arrow keys and value is formerly an maximum edge value
        return roundToStep(rawValue + step / 2);
      }
      if (rawValue === min + step) {
        // Same for minimum edge value
        return roundToStep(rawValue - step / 2);
      }
      return roundToStep(rawValue);
    }
    return rawValue;
  }

  return defaultValueReducer(rawValue, props, event);
}
const styles = theme => ({
  card: {
    maxWidth: 400
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  details: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "center"
  },
  reader: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    margin: "0px 10px"
  },
  controls: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    paddingLeft: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    justifyContent: "center"
  },
  playIcon: {
    height: 38,
    width: 38
  },
  progress: {
    display: "flex",
    margin: "0px 10px"
  },
  slider: {
    padding: "8px 0px",
    width: "100%"
  },

  avatar: {
    backgroundColor: red[500]
  }
});
var diss;
class Player extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      play: false,
      pause: true,
      value: 0,
      surahTitle: "Suratul Fatiha",
      secondsLabel: 0,
      minutesLabel: 0,
      hourLabel: 0,
      duration: 0,
      totSec: 0,
      totHr: 0,
      totMin: 0,
      surahList: [],
      playingId: undefined,
      playing: false,
      url:
        "http://server11.mp3quran.net/shatri/001.mp3"
    };
    this.audio = new Audio(this.state.url);
    diss = this;
    console.log(diss);
  }
  handleChange = (event, value) => {
    this.setState({ value });
    var minutes = Math.floor(value / 60);
    var seconds = Math.floor(value - minutes * 60);
    this.audio.currentTime = value;
  };

  componentDidMount() {
    fetch(
      "https://mp3quran.net/api/_english_sura.json"
    )
      .then(data => data.json())
      .then(data => {
        this.setState({
          surahList: data.Suras_Name
        });
      })
      .catch(err => console.log(err));
  }

  play = () => {
    //this.audio.src = this.state.url;
    var duration = this.audio.duration;
    var minutes = Math.floor(duration / 60);
    var seconds = Math.floor(duration - minutes * 60);
    this.setState({
      play: true,
      pause: false,
      duration: duration,
      totMin: minutes,
      totSec: seconds
    });
    this.audio.play();
    setInterval(() => {
      var curTime = this.audio.currentTime;
      if (this.audio.ended) {
        this.setState({
          play: false,
          pause: true,
          value: 1,
          secondsLabel: 0,
          minutesLabel: 0,
          hourLabel: 0
        });
        this.next();
      }
      this.setState({
        value: curTime.toFixed(1),
        secondsLabel: Math.round(curTime % 60),
        minutesLabel: Math.floor(curTime / 60)
      });
    }, 1000);
  };
  pad = e => {
    var url = "http://server11.mp3quran.net/shatri/";
    var val = "";
    if (e < 10) {
      val = url + "00" + e + ".mp3";
    } else if (e < 100) {
      val = url + "0" + e + ".mp3";
    } else {
      val = url + e + ".mp3";
    }
    return val;
  };

  togglePlay = () => {
    if (diss.state.play) {
      diss.pause();
    } else {
      diss.play();
    }
  };

  getSurah(e, title) {
    var url = "http://server11.mp3quran.net/shatri/";
    diss.setState({
      playingId: e,
      playing: true
    });
    var val = diss.pad(e);
    diss.state.surahTitle = title;
    diss.state.url = val;
    diss.audio.src = diss.state.url;
    diss.audio.load();
    diss.audio.oncanplaythrough = function() {
      diss.togglePlay();
    };
  }
  next = () => {
    var str = this.state.url;
    var last = str.lastIndexOf("/");
    var rmp3 = str.slice(last, str.lenght);
    var res = rmp3
      .replace(".mp3", "")
      .replace("/", "");
    var url = "http://server11.mp3quran.net/shatri/";
    var increment = Number(res);
    var title = this.state.surahList.filter(item => {
      return Number(item.id) === increment;
    });
    console.log(title[0].name);
    increment++;
    if (increment > 114) {
      title = this.state.surahList.filter(item => {
        return Number(item.id) === 1;
      });
      url = url + "001.mp3";
    } else {
      title = this.state.surahList.filter(item => {
        return Number(item.id) === increment;
      });
      url = this.pad(increment);
    }
    this.state.surahTitle = title[0].name;
    this.state.url = url;
    this.audio.src = this.state.url;
    this.audio.load();
    this.audio.oncanplaythrough = () => {
      this.play();
    };
  };
  prev = () => {
    var str = this.state.url;
    var last = str.lastIndexOf("/");
    var rmp3 = str.slice(last, str.lenght);
    var res = rmp3
      .replace(".mp3", "")
      .replace("/", "");
    var url = "http://server11.mp3quran.net/shatri/";
    var decrement = Number(res);
    decrement--;
    var title;
    if (decrement < 1) {
      title = this.state.surahList.filter(item => {
        return Number(item.id) === 114;
      });
      url = url + "114.mp3";
    } else {
      title = this.state.surahList.filter(item => {
        return Number(item.id) === decrement;
      });
      url = this.pad(decrement);
    }

    this.state.surahTitle = title[0].name;
    this.state.url = url;
    this.audio.src = this.state.url;
    this.audio.load();
    this.audio.oncanplaythrough = () => {
      this.play();
    };
  };

  pause = () => {
    this.setState({
      play: false,
      pause: true
    });
    this.audio.pause();
  };
  replay = () => {
    this.setState({
      play: true,
      pause: false
    });
    this.audio.loop = true;
    this.play();
  };

  render() {
    const { classes, theme } = this.props;
    const {
      minutesLabel,
      value,
      secondsLabel,
      totSec,
      totMin,
      surahList
    } = this.state;
    let play_pause;
    if (this.state.play) {
      play_pause = (
        <IconButton
          onClick={this.pause}
          aria-label="Play/pause"
        >
          <PauseIcon className={classes.playIcon} />
        </IconButton>
      );
    } else {
      play_pause = (
        <IconButton
          onClick={this.play}
          aria-label="Play/pause"
        >
          <PlayArrowIcon
            className={classes.playIcon}
          />
        </IconButton>
      );
    }
    return (
      <div className="App">
        <Card className={classes.card}>
          <CardHeader
            avatar={
              <Avatar
                aria-label="Recipe"
                className={classes.avatar}
              >
                Q
              </Avatar>
            }
            action={
              <IconButton>
                <MoreVertIcon />
              </IconButton>
            }
            title="Quran Al-Kareem"
            subheader={this.state.surahTitle}
          />
          <CardMedia
            className={classes.media}
            image="https://previews.123rf.com/images/asep17/asep171705/asep17170500024/78357725-background-ramadan-kareem-with-al-quran-.jpg"
            title="Quran"
          />
          <div className={classes.progress}>
            <Slider
              classes={{
                container: classes.slider
              }}
              value={value}
              valueReducer={valueReducer}
              min={0}
              max={this.state.duration}
              step={1}
              aria-labelledby="label"
              onChange={this.handleChange}
            />
          </div>
          <div className={classes.reader}>
            <Typography
              variant="caption"
              color="textSecondary"
            >
              {minutesLabel}:{secondsLabel}
            </Typography>
            <Typography
              variant="caption"
              color="textSecondary"
            >
              {totMin}:{totSec}
            </Typography>
          </div>
          <div className={classes.details}>
            <div className={classes.controls}>
              <IconButton
                onClick={this.prev}
                aria-label="Previous"
              >
                {theme.direction === "rtl" ? (
                  <SkipNextIcon />
                ) : (
                  <SkipPreviousIcon />
                )}
              </IconButton>
              {play_pause}

              <IconButton
                onClick={this.next}
                aria-label="Next"
              >
                {theme.direction === "rtl" ? (
                  <SkipPreviousIcon />
                ) : (
                  <SkipNextIcon />
                )}
              </IconButton>
              <IconButton
                onClick={this.replay}
                aria-label="Replay"
              >
                <ReplayIcon />
              </IconButton>
            </div>
          </div>
        </Card>
        <SurahLists
          getSurah={this.getSurah}
          surahList={surahList}
          id={this.state.playingId}
        />
      </div>
    );
  }
}

Player.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default withStyles(styles, {
  withTheme: true
})(Player);
